/**
 * 
 * @param {number} lat 
 * @param {number} lng 
 * @param {string} address 
 */
android.openBmap = (data) => {
  let url = ""
  switch(localStorage.getItem('app_type')) {
    case '1':
      url = "baidumap://map/marker?location=" + data.lat + "," + data.lng + "&title=" + "&content=" + data.address + "&traffic=on&src=wz";
      break
    case '2':
      url = "baidumap://map/marker?location=" + data.lat.toFixed(6) + "," + data.lng.toFixed(6) + "&title=&content=" + data.address + "&src=wz";
      url = encodeURI(url);
      break
  }
  // alert(url)
  android.toUrl(url)
}
