<template>
  <div class="shopDetail">
    <div class="header">
      <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
      <span></span>
    </div>
    <div class="main_container">
      <p class="main_container_item" @click="changeLogo">
        <span class="shopName"><i>*</i> LOGO</span>
        <span><img :src="logoImgUrl" alt=""></span>
        <i class="i"><img src="../../assets/shopDetail/hint.png" alt=""> 250*250</i>
        <span class="span3"><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <p class="main_container_item" @click="changeName">
        <span><i>*</i> </span>
        <span :class="{'achive': name !== '' }">{{ name }}</span>
        <span><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <p class="main_container_item" @click="changeAddress">
        <span><i>*</i> </span>
        <span :class="{'achive': address !== '' }">{{ address + doorAdd }}</span>
        <span><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <p class="main_container_item" @click="changeShopPhoneNum">
        <span><i>*</i> </span>
        <span :class="{'achive': shopPhoneNum !== '' }">{{ shopPhoneNum }}</span>
        <span><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <!-- <p class="main_container_item" @click="changeTags">
        <span :class="{'achive': tags !== '', flag: true}"></span>
        <span class="set">{{ tags }}</span> -->
        <!-- <span><img src="../../assets/my/more.png" alt=""></span> -->
      <!-- </p> -->
      <p class="main_container_item">
        <span><i>*</i> </span>
        <span class="achive">{{ showed }}</span>
      </p>
      <p class="main_container_item" @click="changeDesc">
        <span><i>*</i> </span>
        <span :class="{'achive': shopDesc !== '' }">{{ shopDesc }}</span>
        <span><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <p class="main_container_item height" @click="skip_fc">
        <span><i>*</i> </span>
        <span class="span2">
          <img src="../../assets/shopDetail/added.png" alt=""  v-if="mien">
          <img :src="mien" alt="" class="bgi" v-if="mien">
          <img src="../../assets/shopDetail/add.png" alt="">
          <i class="posi">
            <img src="../../assets/shopDetail/hint.png" alt=""> 
            640*640 <br>
            8
          </i>
        </span>
        <span class="span3"><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <p class="main_container_item height"  @click="$router.push({name :'photoshow', query: { id: 'aptitude' }})">
        <span><i>*</i> </span>
        <span class="span2">
          <img src="../../assets/shopDetail/added.png" alt=""  v-if="aptitude">
          <img :src="aptitude" alt="" class="bgi" v-if="aptitude">
          <img src="../../assets/shopDetail/add.png" alt="">
          <i class="posi">
            <img src="../../assets/shopDetail/hint.png" alt=""> 
             <br>
            
          </i>
        </span>
        <span class="span3"><img src="../../assets/my/more.png" alt=""></span>
      </p>
      <p class="main_container_item height" @click="$router.push({name :'photoshow', query: { id: 'promise' }})">
        <span><i>*</i> </span>
        <span class="span2 ">
          <img src="../../assets/shopDetail/added.png" alt=""  v-if="promise">
          <img :src="promise" alt="" class="bgi" v-if="promise">
          <img src="../../assets/shopDetail/add.png" alt="">
          <i class="posi posi1 alone">
            <img src="../../assets/shopDetail/hint.png" alt=""> 
            
          </i>
        </span>
        <span class="span3 "><img src="../../assets/my/more.png" alt=""></span>
      </p>
    </div>
    <mt-button class="btn" @click="goback" ref="btn2"></mt-button>
  </div>
</template>
<script>
import { Base64 } from 'js-base64'
import { eventBus } from '../../utils/eventBus';
export default {
  data() {
    return {
      logoImgUrl: require('../../assets/shopDetail/logo.png'),
      name: '',
      shopPhoneNum: '',
      address: '',
      tags: '',
      doorAdd: '',
      showed: '',
      shopDesc: '',
      lat: '',
      lng: '',
      mien: '',
      aptitude: '',
      promise: ''
    }
  },
  methods: {
		skip_fc(){
			localStorage.removeItem('fz')
			this.$router.push({name :'grouping', query: { id: 'photoShow' }})
		},
    getData() {
      this.loading()
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.get),
        method: 'post',
        data: this.$http.adornData('', this.apiList.shop.get, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          this.name = data.data.name ? data.data.name : ''
          this.address = data.data.address ? data.data.address : ''
          this.shopPhoneNum = data.data.shopPhoneNum ? data.data.shopPhoneNum : ''
          this.tags = data.data.tags.length == 2 ? data.data.tags[0].name + "   " + data.data.tags[1].name : data.data.tags.length == 1 ? data.data.tags[0].name : ''
          this.shopDesc = data.data.shopDesc ? '' : ''
          this.doorAdd = data.data.doorAdd
          this.lat = data.data.lat
          this.lng = data.data.lng
          this.logoImgUrl = data.data.logoImgUrl ? data.data.logoImgUrl : require('../../assets/shopDetail/logo.png')
          localStorage.setItem('shopMess_logoUrl', data.data.logoImgUrl)
          localStorage.setItem('shopMess_shopName', data.data.name)
        } else {
          console.log(11111)
          if (data.msg === 'sign' || data.msg === 'userIdsign') {
            localStorage.clear()
            const instance = this.$toast({
              message: '',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    changeLogo() {
      const vm = this
      // 
      if(!this.isAuth('/moduleShop/modSelf')) return this.$toast('')
      
      window.goLogoClip = function (data) {
        vm.$router.push({ name: 'shopDetail_logo', params:{imgdata: data}})
      }
      this.android.getImg('goLogoClip')
    },
    changeName() {
      if(!this.isAuth('/moduleShop/modSelf')) return this.$toast('')
      this.$router.push({name: 'shopDetail_name', params: {name: this.name}})
    },
    changeAddress() {
      if(!this.isAuth('/moduleShop/modSelf')) return this.$toast('')
      this.$router.push({name: 'shopDetail_address', params: {address: this.address, doorAdd: this.doorAdd, lat: this.lat, lng: this.lng}})
    },
    changeShopPhoneNum() {
      if(!this.isAuth('/moduleShop/modSelf')) return this.$toast('')
      this.$router.push({name: 'shopDetail_phone', params: {shopPhoneNum: this.shopPhoneNum}})
    },
    changeDesc() {
      if(!this.isAuth('/moduleShop/modSelf')) return this.$toast('')
      this.$router.push('shopDetail_desc')
    },
    changeTags() {
      this.$toast('')
    },
    goback() {
      this.$router.goback()
    },
    getMien() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.photoShow.list),
        method: 'post',
        data: this.$http.adornData( '' , this.apiList.photoShow.list, true)
      }).then(({ data }) => {
        console.log(data)
        if (data.result) {
          if(data.data.length>0){
            this.mien = data.data[0].imgUrl
          }
          console.log(this.mien)
          // this.$nextTick(() => {
          //   this.$previewRefresh()
          // })
        } else {
          if (data.msg === 'sign') {
            localStorage.clear()
            const instance = Toast({
              message: '',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    getAptitude() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.aptitude.list),
        method: 'post',
        data: this.$http.adornData( '' , this.apiList.aptitude.list, true)
      }).then(({ data }) => {
        console.log(data)
        if (data.result) {
          if(data.data.length>0){
            this.aptitude = data.data[0].imgUrl
          }
        } else {
          if (data.msg === 'sign') {
            localStorage.clear()
            const instance = Toast({
              message: '',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    getPromise() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.promise.list),
        method: 'post',
        data: this.$http.adornData( '' , this.apiList.promise.list, true)
      }).then(({ data }) => {
        if (data.result) {
          if(data.data.length>0){
            this.promise = data.data[0].imgUrl
          }
        } else {
          if (data.msg === 'sign') {
            localStorage.clear()
            const instance = Toast({
              message: '',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
  },
  created(){
    this.getMien()
    this.getAptitude()
    this.getPromise()
  },
  mounted() {
    this.getData()
    // eventBus.$on('logoChangeSuccess', (res) => {
    //   this.logoImgUrl = res
    //   localStorage.setItem('shopMess_logoUrl', this.logoImgUrl)
    // })
    //nameChangeSuccess
    // eventBus.$on('nameChangeSuccess', (res) => {
    //   this.name = res
    //   localStorage.setItem('shopMess_shopName', this.name)
    // })
    // eventBus.$on('phoneChangeSuccess', (res) => {
    //   this.shopPhoneNum = res
    // })
     // 
    var h = this.$refs.btn2.$el.getBoundingClientRect().top + 52
    // 
    var  clientHeight = document.documentElement.clientHeight
    if(clientHeight > h){
      // alert(1)
      this.$refs.btn2.$el.style.marginTop = clientHeight - h +46+'px'
    }
  }
}
</script>
<style lang="scss">
  .shopDetail{
    position: relative;
    width: 100%;
    top: 0;
    bottom: 0;
    background: #f5f5f5;
    .main_container{
      // height: 37.4rem;
      width: 100%;
      background: #fff;
      margin-top: 2rem;
      box-sizing: border-box;
      padding: 0 1.5rem;
    }
    .main_container_item{
      border-bottom: solid .1rem #f5f5f5;
      width: 100%;
      height: 5rem;
      line-height: 5rem;
      .flag{
        padding-left: 1rem;
      }
      .set{
        margin-left: -1rem;
      }
      span{
        display: inline-block;
        font-size: 1.4rem;
        vertical-align: middle;
        text-align: left;
        i{
          color: #FF3B30;
        }
      }
      span:nth-child(1){
        width: 8.1rem;
        margin-right: 2.8rem;
      }
      span:nth-child(2){
        color: #ccc;
        width: 18.8rem;
        // overflow: hidden;
        // text-overflow:ellipsis;
        // white-space: nowrap;
      }
      .achive{
        color: #666666!important;
        white-space:nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .span2{
        line-height: 5rem;
        height: 5rem;
        position: relative;
        img{
          width: 4rem;
          margin-top: .5rem;
          margin-right: 1rem;
          // vertical-align: middle;
        }
        .bgi{
          width: 4rem;
          height: 4rem;
          position: absolute;
          left: 0;
          // z-index: -5;
          // border: 4rem;
          border-radius: .3rem; 
        }
      }
       span:nth-child(3),.span3{
        float: right;
        width: .8rem;
        height: 1.4rem;
        img{
          width: 100%;
          height: 100%;
          transform: translateY(.4rem);
        }
      }
      .i {
        font-style: normal;
        font-size: 1.1rem;
        color:rgba(102,102,102,1);
        margin-left: 1.55rem;
        img{
          width: 1.1rem;
          height: 1.1rem;
          // vertical-align: middle;
        }
      }
    }
    .main_container_item:first-child{
      height: 7rem;
      line-height: 7rem;
      span:nth-child(2){
        width: 4rem;
        height: 4rem;
        line-height: 4rem;
        text-align: center;
        box-sizing: border-box;
        border: solid .1rem #fafafa;
        img{
          margin: auto;
          // width: 2.4rem;
          height: 2.9rem;
          vertical-align: middle;
        }
      }
    }
    .height{
      height: 7rem;
      line-height: 7rem;
    }
    .posi{
      position: absolute;
      width: 12.5rem;
      height: 2.7rem;
      font-size: 1.1rem;
      font-style: normal;
      line-height: 1.6rem;
      color: #666666 !important; 
      margin-top: 1.1rem;
      margin-left: .9rem;
      img{
        width: 1.1rem !important;
        height: 1.1rem;
        position: absolute;
        top: 25%;
        left: -12%;
        // vertical-align: middle;
      }
    }
    .posi1{
      img{
        top: -13%;
      }
    }
    .alone{
      top: .6rem;
    }
    .btn{
      width: 100%;
      margin-top: 4.2rem;
      background-color: #E63C39;
      color: #fff;
      font-size: 1.6rem;
      height: 4.75rem;
      border-radius: 0;
    }
  }
</style>


