<template>
<div class="addbankList">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span></span>
  </div>
  <div class="marin_body">
    <div class="item">
      <span></span> <input type="text" v-model="personName" placeholder="" >
    </div>
    <div class="item">
      <span></span><input  v-model="cardNum" @change="bankTypeChange" placeholder="" class="suo">
    </div>
    <div class="item">
      <span></span><input  v-model="bankName" @click="bankTypeChange" placeholder="" class="suo" readonly="readonly">
    </div>   
  </div>
  <mt-button @click="addBank" class="addbtn"></mt-button>
</div>
</template>
<script>
import { bankCardAttribution } from '../../utils/bankList'
export default {
  data() {
    return{
      personName: '',
      cardNum: '',
      bankCode: '',
      bankName: '',
    }
  },
  watch:{
    // 
    cardNum(newValue ,oldValue){
      // 
      if (/\S{5}/.test(newValue)) {
         this.cardNum = newValue.replace(/\s/g, '').replace(/(.{4})/g, "$1 ");
      }
      // 
      var str = newValue.replace(/\s/g, '').replace(/(.{4})/g, "$1 ").replace(/\s*/g,"")
      if(str.length>18){
        this.cardNum = str.substr(0, 19).replace(/\s/g, '').replace(/(.{4})/g, "$1 ")
      }
      // 
      if(str.replace(/[^\D]/g,'')){
        // this.$toast('')
        console.log(str)
        if(!parseInt(str)){
          return this.cardNum = ""
        }
        return this.cardNum = ""
      }
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    addBank() {
      if (! this.bankCode) {
        return this.$toast('')
      }
      if (! this.personName) {
        return this.$toast('')
      }
      const url = this.apiList.bankCard.add
      this.upload()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ personName: this.personName, cardNum: this.cardNum.replace(/(.{4})/g, "$1 ").replace(/\s*/g,""), bankCode: this.bankCode }, url, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          // 
          this.$toast({
            message: '',
            iconClass: 'fa fa-check fa-5x'
          })
          setTimeout(() => {
            this.goback()
          }, 1500);
        } else {
          this.$toast(data.msg)
        }
      })
    },
    bankTypeChange() {
      console.log("",this.cardNum) 
      var str = this.cardNum.replace(/\s*/g,"")
      console.log("",str)
      const result = bankCardAttribution(str)
      if (result !== 'error') {
        // 
        this.bankCode = result.bankCode
        this.bankName = result.bankName
      } else {
        this.bankCode = null
        this.$toast('')
      }
    },
  },
}
</script>
<style lang="scss">
  .addbankList{
    .suo{
      margin-left: .4rem;
      width: 17.5rem;
    }
    position: fixed;
    top: 0;
    bottom: 0;
    background: #f5f5f5;
    .marin_body{
      margin-top: 1.5rem;
      background: #fff;
      width: 100%;
      .item{
        background: #fff;
        padding:1.4rem 0;
        margin: 0 2rem;
        border-bottom: solid #EEEEEE .1rem;
        box-sizing: border-box;
        span{
          display: inline-block;
          color: #171717;
          font-size: 1.4rem;
          text-align: left;
          width: 8rem;
        }
        input{
          border: none;
          color: #666666;
          font-size: 1.4rem;
          outline: none;
          // height: 95%;
          // height: 9rem;
          height: 2.7rem;
        }
         input::-webkit-input-placeholder {
          /* placeholder  */
          color: #aab2bd;
        }
      }
      .item:last-child{
        border: none;
      }
    }
    .addbtn{
      display: block;
      width: 31.6rem;
      height: 4.4rem;
      line-height: 4.4rem;
      text-align: center;
      font-size: 1.4rem;
      color: #fff;
      background: #e51d27;
      margin: 9.7rem auto 0;
      border-radius: 0;
    }
    .van-cell {
      position: relative;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      box-sizing: border-box;
      width: 100%;
      padding: 1.4rem 2rem;
      overflow: hidden;
      color: #323233;
      font-size: 1.4rem;
      line-height: 29.8px;
      background-color: #fff;
      span{
        font-size: 1.4rem;
      }
      .van-field__body{
        input{
          border: none;
          color: #666666;
          font-size: 1.4rem;
          outline: none;
          // height: 95%;
          // height: 9rem;
          height: 2.7rem;
        }
        input::-webkit-input-placeholder {
          /* placeholder  */
          color: #aab2bd;
        }
      }
    }
  }
</style>


